.divider {
  display: flex;
  align-items: center;

  &::after,
  &::before {
    content: '';
    height: var(--ui-border-width-small);
    background-color: var(--ui-color-border-primary);
    width: 50%;
  }

  &__text {
    white-space: nowrap;
    padding: 0 var(--ui-element-indent-medium);
  }
}